.p-contact {
  position: relative;
  z-index: -2;
  padding: rem(40) 0;
  background-color: #f2f2f2;

  @include mq (tab) {
    padding-top: rem(60);
    padding-bottom: rem(120);
  }
}

.p-contact__wrapper {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.p-contact__img {
  width: 100%;
  position: relative;

  @include mq (tab) {
    max-width: rem(500);
  }
}

.p-contact__img::before {
  padding-top: 67.2463768%;// (232/345)
  display: block;
  content: "";


}

.p-contact__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-position: top;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.p-contact__body {
  margin-top: rem(30);

  @include mq (tab) {
    max-width: rem(440);
    width: 100%;
    margin-right: rem(110);
  }
}

.p-contact__text {
  margin-top: rem(30);
}

.p-contact__btn {
  margin-top: rem(30);
}